import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/components/Home"
import Works from "@/components/Works"

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/works',
    component: Works
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
